.connect {
  position: fixed;
  top: 5%;
  left: 5%;
}

.container {
  margin-top: 16px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100vw;
  padding: 0 24px;
}

.collectionContainer {
  width: 1200px;
  max-width: 90vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 0px;
}

.h1 {
  margin-bottom: 0px;
}

.explain {
  font-size: 1.125rem;
}

.divider {
  width: 50%;
  border-color: grey;
  opacity: 0.25;
}

.textInput {
  width: 75%;
  background-color: transparent;
  border: 1px solid grey;
  border-radius: 8px;
  color: #fff;
  height: 48px;
  font-size: 1rem;
}

.tokenGrid {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 1%;
  width: 2000%;
  max-width: 110vw;
  flex-wrap: wrap;
  margin-top: 16px;
}

.tokenItem {
  width: 45%;
  height: 180px;
  border-radius: 16px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border: 1px solid rgba(255, 255, 255, 0.4);
}

.tokenLabel {
  margin-top: 0px;
  margin-bottom: 0px;
  font-weight: 500;
}

/* Second tokenLabel item */

.tokenValue {
  font-size: 1.3rem;
  margin-top: 8px;
  margin-bottom: 0px;
  font-weight: 500;
}

/* On Mobile, make tokenGrid 100% width */
@media only screen and (max-width: 850px) {
  .tokenItem {
    width: 90%;
    margin-top: 12px;
  }
}

.amountToClaim {
  align-items: center;
  justify-content: center;
  display: flex;
  flex-direction: row;
  gap: 16px;
  margin-top: 16px;
}

.noGapBottom {
  margin-bottom: 0px;
}

.berc20Address {
  width: 90%;
  align-items: center;
  justify-content: center;
  display: flex;
  flex-direction: row;
  gap: 16px;
  margin-top: 16px;
}

.spAddress {
  width: 60%;
  align-items: center;
  justify-content: center;
  display: flex;
  flex-direction: row;
  gap: 16px;
  margin-top: 16px;
}

.spGrid {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 10px;
  width: 80%;
  max-width: 500vw;
  /* max-width: 500vw; */
  /* flex-wrap: wrap; */
  margin-top: 16px;
}

.spItem {
  width: 50%;
  height: 170px;
  border-radius: 16px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* gap: 16px; */
  border: 1px solid rgba(255, 255, 255, 0.4);
}